<script>
import Header from "../../components/global/header/HeaderArea.vue"
import NoData from "../../components/global/NoData.vue"
import { ref, onMounted, onUnmounted } from 'vue'
import { apiGetMyCourseList, apiGetCourseList } from "@/api/api"
import { useRouter } from "vue-router"
import { isBetweenBottom } from "@/utils/helper"

export default {
  setup() {
    const router = useRouter()
    let courseData = ref([])
    let total = ref(0)
    let APIparams = ref({ page: 1, limit: 10 })
    let getApiTimer = null

    onMounted(async () => {
      courseData.value = await apiGetMyCourseList(APIparams.value)
      total.value = courseData.value.data.total
      courseData.value = courseData.value.data.data
      // console.log(courseData.value)

      //每0.5S監測是否在底部
      getApiTimer = setInterval(() => {
        if (isBetweenBottom()) {
          // 超過上限不做GET
          if (courseData?.value?.length < total.value) {
            handleScroll();
          }
        }
      }, 500)
    })

    //滾動載入資料
    const handleScroll = async () => {
      APIparams.value.page += 1;
      let newData = ref()
      newData.value = await apiGetMyCourseList(APIparams.value)
      courseData.value = courseData.value.concat(newData.value.data.list);
    };
    //取消監控
    onUnmounted(() => clearInterval(getApiTimer))

    const goto = (val) => {
      router.push({ path: val })
    }

    return { courseData, total, goto };
  },
  components: {
    Header,NoData
  }
};
</script>

<template>
  <Header title="我的課程" backToPath="/course/index" />
  <div class="myCourse">
    <section class="c-main">
      <NoData v-if="total == 0" />
      <div class="course-container">
        <div class="card" v-for="item in courseData" :key="item.id">
          <div class="card-body">
            <a @click="goto(`/course/info/${item.courseId}`)" class="card-link">
              <div class="card-title">{{ item.name }}</div>
              <div class="card-date">課程日期 : {{ item.startDate.slice(0, 10) }} ~ {{ item.endDate.slice(0, 10) }}</div>
              <div class="card-organize">培訓單位 : {{ item.organizer }}</div>
              <div class="card-location">縣市地區 : {{ item.address }}</div>
              <div class="card-hour">訓練時數 : {{ item.hours }}</div>
              <div class="card-hour">報名資訊 : 張曉明 / 0933-222-222</div>
            </a>
            <div class="card-state" v-if="(item.coursePaymentStatus == 0)">
              <a href="javascript:void(0);" class="btn btn-state">前往付款</a>
            </div>
            <div class="card-state" v-if="(item.coursePaymentStatus == 1)">
              <a href="schedule-detail.html" class="btn btn-completed">已繳費</a>
            </div>
            <div class="card-state" v-if="(item.coursePaymentStatus == 2)">
              <a href="schedule-detail.html" class="btn btn-notwork">已退款</a>
            </div>
          </div>
        </div>
      </div>
    </section>
  </div>
</template>

<style lang="scss" scoped>
@import "@/assets/custom/config";

.myCourse {
  margin-top: $header-height;
  margin-bottom: calc($footer-height + 15px);
}
</style>